import {
  ADD_PRODUCT,
  REMOVE_PRODUCT,
  CHANGE_PRODUCT_QUANTITY,
} from './actionTypes';

const initialState = {
  products: [],
};

const cartReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_PRODUCT:
      return {
        ...state,
        products: [...state.products, action.payload],
      };
    case REMOVE_PRODUCT:
      return {
        ...state,
        products: state.products.filter(product => product === action.payload),
      };
    case CHANGE_PRODUCT_QUANTITY:
      const products = state.products;
      const productIndex = state.products.products.findIndex(
        product => product.id === action.payload.id,
      );
      products[productIndex].quantity = action.payload.quantity;

      return {
        ...state,
        products: state.products,
      };
    default:
      return state;
  }
};

export {cartReducer};
